import React, { Component } from "react";
import { Carousel } from 'antd';
import { withRouter, Link } from "react-router-dom";
// import "react-responsive-carousel/lib/styles/carousel.min.css";
// import { Carousel } from 'react-responsive-carousel';
import "./index.scss";

// import "slick-carousel/slick/slick.css";
// import "slick-carousel/slick/slick-theme.css";
// import Slider from "react-slick";

import Header from "../../components/header/index";
import Footer from "../../components/footer/index";

const ChooseDate = {
  date1: { name: 'date1', date: '20200812 | 14:00'},
  date2: { name: 'date2', date: '20200813 | 14:00'},
  date3: { name: 'date3', date: '20200814 | 14:00'},
  date4: { name: 'date4', date: '20200815 | 14:00'},
  date5: { name: 'date5', date: '20200816 | 14:00'}
}

class Consultation extends Component {
  constructor() {
    super();
    this.state = {
      chooseDate: ChooseDate.date1
    }
  }

  _onMouseEnter(date) {
    this.setState({ chooseDate: date });
  }

  // 
  _renderCarousel() {
    return (
      <Carousel className="banner-carousel" autoplay>
        <div>
          <img src={require("../../assets/img/consultation/tation.png")} />
        </div>
        <div>
          <img src={require("../../assets/img/consultation/tation.png")} />
        </div>
        <div>
          <img src={require("../../assets/img/consultation/tation.png")} />
        </div>
        <div>
          <img src={require("../../assets/img/consultation/tation.png")} />
        </div>
      </Carousel>
    )
  }

  render() {
    const { chooseDate } = this.state;
    return (
      <div>
        <Header />
        <div className="carousel-wrapper">
          {this._renderCarousel()}
        </div>

        <div className="consultation">
          <img className="title-img" src={require("../../assets/img/consultation/live-title.png")} />
          <div className="content-1">
            <div className="content-1-left">
              <div className={chooseDate.name === ChooseDate.date1.name ? "choose" : ""} onMouseEnter={() => this._onMouseEnter(ChooseDate.date1)}>
                <p>0812</p>
                <p>14:00</p>
              </div>
              <div className={chooseDate.name === ChooseDate.date2.name ? "choose" : ""} onMouseEnter={() => this._onMouseEnter(ChooseDate.date2)}>
                <p>0813</p>
                <p>14:00</p>
              </div>
              <div className={chooseDate.name === ChooseDate.date3.name ? "choose" : ""} onMouseEnter={() => this._onMouseEnter(ChooseDate.date3)}>
                <p>0814</p>
                <p>14:00</p>
              </div>
              <div className={chooseDate.name === ChooseDate.date4.name ? "choose" : ""} onMouseEnter={() => this._onMouseEnter(ChooseDate.date4)}>
                <p>0815</p>
                <p>14:00</p>
              </div>
              <div className={chooseDate.name === ChooseDate.date5.name ? "choose" : ""} onMouseEnter={() => this._onMouseEnter(ChooseDate.date5)}>
                <p>0816</p>
                <p>14:00</p>
              </div>
            </div>
            <div className="content-1-center">
              <p className="tip-1">
                <img src={require("../../assets/img/consultation/datePicker.png")} />
                <span>{chooseDate.date}</span>
              </p>
              <p className="tip-2">-5G</p>
              <p className="tip-3">5GAI-5G</p>
              <p className="tip-4">1IT</p>
              <p className="tip-4">2IT</p>
              <Link to="/digital" ><p className="tip-5"></p></Link>
            </div>
            <div className="content-1-right">
              <img className="live-img" src={require("../../assets/img/consultation/live.png")} />
              <img src={require("../../assets/img/consultation/img_content_1.png")} />
            </div>
          </div>
          <img className="title-img" src={require("../../assets/img/consultation/project-title.png")} />
          <div className="content-2">
            <img className="content-2-left" src={require("../../assets/img/consultation/last_step.png")} />
            <div>
              <Link to="/digital" >
                <img src={require("../../assets/img/consultation/img_content_1.png")} />
              </Link>
            </div>
            <div>
              <Link to="/onlineDay" >
                <img src={require("../../assets/img/consultation/img_content_2.png")} />
              </Link>
            </div>
            <img className="content-2-right" src={require("../../assets/img/consultation/next_step.png")} />
          </div>
          <img className="title-img" src={require("../../assets/img/consultation/article-title.png")} />
          <Link className="more" to="/article">
            <p>{">"}</p>
          </Link>
          <div className="content-3">
            <div>
              <img src={require("../../assets/img/consultation/img_content_1.png")} />
              <p className="tip-1">-5G</p>
              <p className="tip-2">5GAI5G</p>
            </div>
            <div>
              <img src={require("../../assets/img/consultation/img_content_3.png")} />
              <p className="tip-1">-5G</p>
              <p className="tip-2">5GAI5G</p>
            </div>
            <div>
              <img src={require("../../assets/img/consultation/img_content_4.png")} />
              <p className="tip-1">-5G</p>
              <p className="tip-2">5GAI5G</p>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
export default withRouter(Consultation);
