import React, {Component, useEffect, useState} from "react";
import './index.scss';

function Index() {

  const [title,settitle] = useState([
    {
      name: '5G',
      src: require('../../../../assets/img/home/m1.png'),
      isShow: true,
      text: '5G4K/8K,',
      href:'/industry#indu-bg1'
    },
    {
      name: '5G',
      src: require('../../../../assets/img/home/a1.png'),
      isShow: false,
      text: '5G5G',
      href: '/industry#indu-bg10'
    },
    {
      name: '5G',
      src: require('../../../../assets/img/home/t.png'),
      isShow: false,
      text: '5GV2X',
      href: '/industry#indu-bg3'
    },
    {
      name: '5G',
      src: require('../../../../assets/img/home/l.png'),
      isShow: false,
      text: '5G5G',
      href: '/industry#indu-bg5'
    },
    {
      name: '5G',
      src: require('../../../../assets/img/home/h1.png'),
      isShow: false,
      text: '5G',
      href: '/industry#indu-bg4'
    },
    {
      name: '5G/',
      src: require('../../../../assets/img/home/vr1.png'),
      isShow: false,
      text: 'VRARVR/ARVR/AR5G',
      href: '/industry#indu-bg2'
    },
  ],)
  const [bgBack,setBgBack] = useState({src:require("../../../../assets/img/home/back1.png")})
  const [changeIndex,setChangeIndex] = useState(0);
  const changTab = (e) => {
    setChangeIndex(e);
    setBgBack({src:require("../../../../assets/img/home/back"+(e+1)+".png")})
  }
  return (
    <div className="apply-box">
      <img className="apply-bg" src={bgBack.src}/>
      <ul className="apply-title">
        {title.map((item, index) => (
          <li className={["apply-core",index===changeIndex?"apply-active":null].join(' ')} onClick={() => changTab(index)}>
            <img className={'appicon-'+index} src={item.src}/>
          </li>
        ))}
      </ul>
      <div className="apply-content">
          <a className="content-box" href={title[changeIndex].href}>
            <p className="content-name">{title[changeIndex].name}</p>
            <p className="content-text">{title[changeIndex].text}</p>
          </a>
      </div>
    </div>
  );
}

export default Index;
