import React, {Component} from "react";
import "./index.scss";
import Header from "../../../components/header/index";
import Footer from "../../../components/footer/index";
import {Pagination, Tabs} from 'antd';
import $ from "jquery";

const {TabPane} = Tabs;
export default class induDeatail extends Component {
    state = {
        contentBoxList: [
            {
                text: '1',
                img: require('../../../assets/img/industry/detail/1.gif')
            },
            {
                text: '2',
                img: require('../../../assets/img/industry/detail/2.gif')
            }
        ],
        pageNum: 1,
        pageSize: 1,
    };
    handleClick = (e) => {
        this.setState({
            current: e,
        });
    };
    onChange = (page) => {
        this.setState({
            pageNum: page
        })
    };
    itemRender = (current, type, originalElement) => {
        if (type === "prev") {
            return <a></a>;
        }
        if (type === "next") {
            return <a></a>;
        }
        return originalElement;
    };


    render() {
        return (
            <div className="industry-detail-wrapper">
                <Header/>
                <div className="plan-header-12">

                </div>
                <div className="induDeatail-wrapper">
                    <div className="induDeatail">
                        <div className="left-section">
                            <div className="logo-header">
                                {/*<div className="logo"></div>*/}
                                <span></span>
                            </div>

                            <main>
                                <Tabs animated={false} defaultActiveKey="1">
                                    <TabPane tab=""
                                        key="1">
                                        <div className="key1">
                                            <p></p>
                                            <div className="key1-img"></div>
                                        </div>
                                    </TabPane>
                                    <TabPane tab=""
                                        key="2">
                                        <div className="key2">
                                            <p>1</p>
                                            <p>2</p>
                                            <p>3</p>
                                            <p>4</p>
                                        </div>
                                    </TabPane>
                                    <TabPane tab=""
                                        key="3">
                                        <div className="key3">
                                            <p>
                                                5G
                                            </p>
                                        </div>
                                    </TabPane>
                                    <TabPane tab=""
                                        key="4">
                                        <div className="key4">
                                            <div className="par">
                                                <h1>1O2O</h1>
                                                <p>O2O</p>
                                            </div>
                                            <div className="par">
                                                <h1>2</h1>
                                                <p></p>
                                            </div>
                                            <div className="par">
                                                <h1>3</h1>
                                                <p></p>
                                            </div>
                                            <div className="par">
                                                <h1>4</h1>
                                                <p>SAP</p>
                                            </div>
                                        </div>
                                    </TabPane>
                                    <TabPane tab=""
                                        key="5">
                                        <div className="key5">
                                            {
                                                this.state.contentBoxList.slice((this.state.pageNum - 1) * this.state.pageSize, (this.state.pageNum - 1) * this.state.pageSize + this.state.pageSize).map((item, index) => (
                                                        <div className="contentBoxList"
                                                            key={index}>
                                                            <p>{item.text}</p>
                                                            <div className="gif">
                                                                <img src={item.img}
                                                                    alt=""/>
                                                            </div>
                                                        </div>
                                                    )
                                                )
                                            }

                                            <div className="pagination ">
                                                <Pagination
                                                    onChange={this.onChange}
                                                    total={2}
                                                    pageSize={this.state.pageSize}
                                                    itemRender={this.itemRender}
                                                />
                                            </div>
                                        </div>
                                    </TabPane>
                                </Tabs>
                            </main>
                        </div>
                        <div className="right-section">
                            <h1></h1>
                            <div className="recommend-logo">
                                <div className="recommend-list">
                                    <img src={require("../../../assets/img/industry/detail/re-1.png")}
                                        alt=""/>
                                </div>
                                <div className="recommend-list">
                                    <img src={require("../../../assets/img/industry/detail/re-2.png")}
                                        alt=""/>
                                </div>
                                <div className="recommend-list">
                                    <img src={require("../../../assets/img/industry/detail/re-3.png")}
                                        alt=""/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}
