// blog-frontend/src/components/Home.tsx

import React, { useState, useEffect, useRef, useReducer } from "react";
import {
  Form,
  Input,
  Button,
  Checkbox,
  Col,
  Row,
  Steps,
  Upload,
  message,
  Popover
} from "antd";

import HeaderTop from "../../components/headerTop";
import "./incorporate.scss";
// import "antd/dist/antd.css";
import Avatar from "../../components/updateUser/Upload";
import Title from "../../components/updateUser/title";
import { createCompany, handleTime } from "../../assets/api/index";
import cookie from "../../assets/libs/cookie";
import config from "../../assets/config";

const { Step } = Steps;

const layout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 10 },
};
const tailLayout = {
  wrapperCol: { offset: 0, span: 10 },
};

function Login() {
  const [current, setCurrent] = useState(0);
  const [userName, setUserName] = useState("");
  const [step0Data, setStep0Data] = useState({});
  const [file, setFile] = useState(null);
  const [idJustFile, setIdJustFile] = useState(null);
  const [idBackFile, setIdBackFile] = useState(null);

  const [form] = Form.useForm();

  //  timing  effect
  useEffect(() => {
    var userInfoValue = JSON.parse(cookie.get("userInfoValue"));
    setUserName(userInfoValue.name);
  }, []);
  form.setFieldsValue({
    companyAttachment: file,
    identityCardPic: `${idJustFile},${idBackFile}`,
  });
  const onFinish = (values) => {
    if (current === 0) {
      const data = Object.assign({}, values, {});
      setStep0Data(data);
      setCurrent(1);
    }

    if (current === 1) {
      const data = Object.assign({}, values, step0Data);
      data["status"] = 1;
      data["appid"] = config.appid;
      const userInfo = JSON.parse(cookie.get("userInfo"));
      Object.assign(data, userInfo);
      userInfo["data"] = data;
      // submit data
      const submit = async () => {
        createCompany(userInfo).then((res) => {
          if (res.code === 0) {
            message.success(",2S");
            setTimeout(() => {
              window.location = "/5g-admin";
            }, 2000);
          } else if (+res.code === 21018) {
            message.error("");
          }
        });
      };
      submit();
    }
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  const onChange = (current) => {
    setCurrent(current);
  };

  const back = () => {
    setCurrent(0);
  };

  return (
    <div className="asstion">
      <HeaderTop userName={userName}></HeaderTop>
      <div className="layout asstion-center">
        <div className="box-title"></div>
        <Steps current={current} onChange={onChange} className="ant-steps-box">
          <Step title="" />
          <Step title="" disabled={true} />
          <Step status="process" title="" disabled={true} />
        </Steps>
        {current === 0 ? (
          <div>
            <Title>
              <i></i>
            </Title>
            <div className="step-wrapper">
              <Form
                {...layout}
                scrollToFirstError
                form={form}
                name="basic"
                initialValues={{ remember: true }}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
              >
                <Form.Item
                  label=""
                  name="companyName"
                  rules={[{ required: true, message: "!" }]}
                >
                  <Input />
                </Form.Item>

                <Form.Item
                  label=""
                  name="creditCode"
                  className="code"
                  rules={[
                    { required: true, message: "!" },
                  ]}
                >
                  <Input maxLength={18} />
                </Form.Item>

                <Form.Item
                  label=""
                  name="address"
                  rules={[{ required: true, message: "!" }]}
                >
                  <Input />
                </Form.Item>

                <Form.Item
                  label=""
                  name="officeTel"
                  rules={[{ required: true, message: "!" }]}
                >
                  <Input maxLength={11} />
                </Form.Item>

                <Form.Item
                  // className="company-attachment"
                  label=""
                  name="companyAttachment"
                  rules={[{ required: true, message: "!" }]}
                >
                  <div className="attach-box">
                    <div className="attach-title">
                      <p>
                        1./
                      </p>
                      <p>2.jpgpng10MB</p>
                    </div>
                    <Avatar setFile={setFile} />
                  </div>
                </Form.Item>

                <div className="posi-box">
                  <Form.Item
                    className="left-align"
                    {...tailLayout}
                    name="isAgree"
                    valuePropName="checked"
                    rules={[{ required: true, message: "!" }]}
                  >
                    <Checkbox></Checkbox>
                  </Form.Item>

                  <Form.Item {...tailLayout} className="confirm">
                    <Button type="primary" htmlType="submit">
                      
                    </Button>
                  </Form.Item>
                </div>
                <div className="clearfix" style={{ height: "10px" }}></div>
              </Form>
            </div>
          </div>
        ) : null}

        {current === 1 ? (
          <div>
            <Title>
              
              <i></i>
            </Title>
            <div className="step-wrapper">
              <Form
                {...layout}
                scrollToFirstError
                form={form}
                name="basic"
                initialValues={{ remember: true }}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
              >
                <Form.Item
                  label=""
                  name="managerName"
                  rules={[{ required: true, message: "!" }]}
                >
                  <Input />
                </Form.Item>

                <Form.Item
                  label=""
                  name="identityCard"
                  rules={[{ required: true, message: "!" }]}
                >
                  <Input maxLength={18} />
                </Form.Item>

                <Form.Item
                  label=""
                  name="identityCardPic"
                  rules={[{ required: true, message: "!" }]}
                >
                  <div style={{ display: "flex", flexFlow: "column" }}>
                    <div className="attach-title">
                      <p>
                        1./
                      </p>
                      <p>2.jpgpng10MB</p>
                    </div>
                    <div style={{ display: "flex", marginTop: "20px" }}>
                      <div className="update-box">
                        <Avatar
                          uploadName=""
                          alt=""
                          setFile={setIdJustFile}
                        />
                      </div>

                      <div className="update-box">
                        <Avatar
                          uploadName=""
                          alt=""
                          setFile={setIdBackFile}
                        />
                      </div>
                    </div>
                  </div>
                </Form.Item>

                <div className="posi-box">
                  <Form.Item
                    className="left-align"
                    {...tailLayout}
                    name="isAgree"
                    valuePropName="checked"
                    rules={[{ required: true, message: "!" }]}
                  >
                    <Checkbox></Checkbox>
                  </Form.Item>

                  <Form.Item {...tailLayout} className="confirm">
                    <Button type="primary" onClick={back} className="step">
                      
                    </Button>
                    <Button
                      type="primary"
                      htmlType="submit"
                      className="okSubmit"
                    >
                      
                    </Button>
                  </Form.Item>
                </div>
              </Form>
            </div>
          </div>
        ) : null}

        {current === 2 ? (
          <div className="step-wrapper1">
            <h2>{`${handleTime()}`}</h2>
            <Button type="primary" href="/5g-admin">
              
            </Button>
          </div>
        ) : null}
      </div>
    </div>
  );
}

export default Login;
