import React, { useEffect,useState } from "react";

function MapLink() {
    const [mapLinkList] = useState(
        [
            {
                time:'2019.12',
                detail:'5G'
            },
            {
                time:'2020.4',
                detail:'455G'
            },
            {
                time:'2020.5',
                detail:''
            },
            {
                time:'',
                detail:''
            },
        ]
    );
    const [activeTime,setActiveTime] = useState(0);
  return (
    <div className="mapLink">
        {
            mapLinkList.map((item,index)=>(
                <div className={['banner-link',activeTime === index ? 'active-link' : null].join(' ')} key={index}>
                    <a className="err-disable">
                        <p>{item.time}</p>
                        <p>{item.detail}</p>
                    </a>
                </div>
            ))
        }
        <div className="timeLine">
            <img src={require("../../assets/img/home/timeline1.gif")}
                alt=""/>
        </div>
    </div>
  );
}
export default React.memo(MapLink) ;
