import React, { useState,useEffect, useRef } from "react";
import "./index.scss";
import { Menu } from "antd";
import { useParams } from 'react-router-dom';
import MobileHeader from "../../components/mobileHeader";
import MobileFooter from '.././../components/mobileFooter';
import MobileAppliContent from './detail/index';
const { SubMenu } = Menu;
function MobileApplication(props) {
  let { id = '1' } = useParams();
  const [current,setCurrent] = useState('1');
  const [openKeys, setOpenKeys] = useState(['sub1']);
  const rootSubmenuKeys = ['sub1', 'sub2', 'sub3'];
  // var location =props.match.params.id;
  useEffect(() => {
    setCurrent(id);
    if( 9 <= +id && +id <= 13) {
      setOpenKeys(['sub2']);
    }
  },[])
  const handleClick = e => {
    setCurrent(e.key);
  };
  const onOpenChange = item => {
    const latestOpenKey = item.find(key => openKeys.indexOf(key) === -1);
    if (rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      setOpenKeys(item);
    } else {
      let openKeys = latestOpenKey ? [latestOpenKey] : []
      setOpenKeys(openKeys);
    }
  };
  return (
      <div className="application-wrapper blur-wrapper">
        <MobileHeader img={require('../../assets/img/application/tit-bg.png')}/>
        <div className="m-application-con">
          <div className='application-con-menu'>
            <Menu
                onClick={handleClick}
                selectedKeys={current}
                // defaultOpenKeys={["sub1"]}
                openKeys={openKeys}
                onOpenChange={onOpenChange}
                mode="inline"
            >
              <SubMenu
                  key="sub1"
                  title={
                    <span>
                    <span></span>
                  </span>
                  }
              >
                <Menu.Item key="1"></Menu.Item>
                {/* <Menu.Item key="2">ID Mapping</Menu.Item> */}
                <Menu.Item key="3"></Menu.Item>
                {/* <Menu.Item key="4"></Menu.Item> */}
                {/* <Menu.Item key="5">RCS</Menu.Item> */}
                <Menu.Item key="6" disabled={true}></Menu.Item>
                {/* <Menu.Item key="7" disabled={true}></Menu.Item> */}
                {/* <Menu.Item key="8" disabled={true}></Menu.Item> */}
              </SubMenu>
              <SubMenu
                  key="sub2"
                  title={
                    <span>
                    <span></span>
                  </span>
                  }
              >
                <Menu.Item key="9"></Menu.Item>
                <Menu.Item key="10"></Menu.Item>
                <Menu.Item key="12"></Menu.Item>
                <Menu.Item key="13"></Menu.Item>
                <Menu.Item key="11" disabled={true}></Menu.Item>
                <Menu.Item key="14" disabled={true}></Menu.Item>
              </SubMenu>
              <SubMenu
                  key="sub3"
                  title={
                    <span>
                    <span></span>
                  </span>
                  }
                  selectable={false}
              >
                <Menu.Item key="15" disabled={true}>5G</Menu.Item>
                <Menu.Item key="16" disabled={true}>5G</Menu.Item>
                <Menu.Item key="17" disabled={true}>5G</Menu.Item>
                <Menu.Item key="18" disabled={true}>5G/</Menu.Item>
                <Menu.Item key="19" disabled={true}>5G</Menu.Item>
                <Menu.Item key="20" disabled={true}>5G</Menu.Item>
                <Menu.Item key="21" disabled={true}>5G</Menu.Item>
                <Menu.Item key="22" disabled={true}>5G</Menu.Item>
                <Menu.Item key="23" disabled={true}>5G</Menu.Item>
                <Menu.Item key="24" disabled={true}>5G</Menu.Item>
                <Menu.Item key="25" disabled={true}>5G</Menu.Item>
                <Menu.Item key="26" disabled={true}>5G</Menu.Item>
              </SubMenu>
            </Menu>
            <MobileAppliContent current={current}/>
          </div>
        </div>
        <MobileFooter />
      </div>
  );
}
export default MobileApplication;
